import React , {Component} from 'react'
import UtilGlobal from './global'
const loadedModelNameSpace = [];
// window.lod = loadedModelNameSpace;
function dynamic(options){
    let {app,models=[],component} = options;
    return class extends Component{
        constructor(props){
            super(props);
            this.timer = setTimeout(()=>{
                UtilGlobal.changeState({isOpen:true});
            },500);
            this.load();
        }
        state = {
            AsyncComponent:null
        }
        render(){
            let {AsyncComponent} = this.state;
            if(AsyncComponent){
                return <AsyncComponent {...this.props} />
            }
            return null;
        }
        load = ()=>{
            Promise.all([component(),...models.map(item=>item())]).then(arr=>{
                arr.slice(1).forEach(item=>{
                    if(loadedModelNameSpace.indexOf(item.default.namespace) === -1){
                        loadedModelNameSpace.push(item.default.namespace);
                        app.model(item.default);
                    }
                });
                clearTimeout(this.timer);
                UtilGlobal.changeState({isOpen:false});
                let AsyncComponent = arr[0].default;
                if(this.mounted){
                    this.setState({AsyncComponent});
                }
            })
        }
        componentDidMount(){
            this.mounted = true;
        }
        componentWillUnmount(){
            this.mounted = false;
        }
    }
}
export default dynamic;