import request from '../utils/request'
// 添加用户
export function addUser(payload){
    return request('/user',{
        method:'POST',
        body:payload
    });
}

// 添加身份
export function addIdentity({identity_text}){
    return request(`/user/identity/edit?identity_text=${identity_text}`);
}

// 添加api接口权限
export function addAuthorityApi(payload){
    let arr = [];
    for(let i in payload){
        arr.push(`${i}=${payload[i]}`);
    }
    let str = '?'+arr.join('&');
    

    return request(`/user/authorityApi/edit${str}`);
}

// 添加视图接口权限
export function addAuthorityView(payload){
    let arr = [];
    for(let i in payload){
        arr.push(`${i}=${payload[i]}`);
    }
    let str = '?'+arr.join('&'); 
    return request(`/user/authorityView/edit${str}`);
}

// 给身份设定api接口权限
export function setIdentityApi(payload){
    return request('/user/setIdentityApi',{
        method:'POST',
        body:payload
    })
}

// 给身份设定视图权限
export function setIdentityView(payload){
    return request('/user/setIdentityView',{
        method:'POST',
        body:payload
    });
}

// 更新用户信息（用户名，用户密码，用户身份）
export function updateUser(payload){
    return request('/user/user',{
        method:'PUT',
        body:payload
    });
}

// 展示所有的用户数据
export function showUser(){
    return request('/user/user');
}
// 展示所有的身份数据
export function showIdentity(){
    return request('/user/identity');
}
// 展示所有的api接口权限数据
export function showApiAuthority(){
    return request('/user/api_authority');
}
// 展示身份和api接口数据的关系
export function showApiIdentity(){
    return request('/user/identity_api_authority_relation');
}
// 展示视图权限的数据
export function showViewAuthority(){
    return request('/user/view_authority');
}
// 展示身份和视图权限的关系
export function showViewIdentity(){
    return request('/user/identity_view_authority_relation');
}
// 登录接口

export function loginApi(loginInfo){
    return request('/user/login',{
        method:'POST',
        body:loginInfo
    });
}

// 获取当前用户信息的接口
export function getUserInfo(){
    return request('/user/userInfo');
}

// 根据用户id，返回该用户的视图权限
export function getViewAuthority(user_id){
    let str = `?user_id=${user_id}`;
    return request(`/user/new${str}`);
}