import { getViewAuthority } from '../service/userManger'
export default {
    namespace: 'global',
    state: {
        isLoad: false,
        isOpen: false,
        viewAuthority: []

    },
    reducers: {
        changeState(state, payload) {
            return { ...state, ...payload }
        }
    },
    effects: {
        *getViewAuthority(payload, { call, put }) {
            let { user_id } = payload;
            let result = yield call(getViewAuthority, user_id);
            if (result.code === 1) {
                yield put({ type: 'changeState', viewAuthority: result.data })
            }
        }
    }
}