import dynamic from '../utils/dynamic'
import app from '../app'
import menu from './menu'
import UtilGlobal from '../utils/global'
const {basePath} = UtilGlobal;
export const groups = {
    QUESTIONSMANGER:{
        groupName:'试题管理',groupIcon:'sliders'
    },
    USERMANGER:{
        groupName:'用户管理',groupIcon:'user'
    },
    EXAMMANGER:{
        groupName:'考试管理',groupIcon:'schedule'
    },
    GRADEMANGER:{
        groupName:'班级管理',groupIcon:'project'
    }
}
const routerConfig = [
    {
        path:basePath+'/login',
        component:dynamic({
            component:()=>import('../page/login'),
            models:[()=>import('../model/login')],
            app
        }),
        text:'登录',
        id:'login'
    },
    {
        path:basePath+'/main',
        component:dynamic({
            component:()=>import('../page/main'),
            models:[()=>import('../model/main'),()=>import('../model/login')],
            app
        }),
        text:'主界面',
        id:'main',
        children:[
            // 试题管理
            {
                path:basePath+'/main/addQuestions',
                component:dynamic({
                    component:()=>import('../page/addQuestions'),
                    models:[()=>import('../model/addQuestions')],
                    app
                }),
                text:'添加试题',
                groupName:groups.QUESTIONSMANGER.groupName,
                groupIcon:groups.QUESTIONSMANGER.groupIcon,
                id:'main-addQuestions'
            },
            {
                path:basePath+'/main/questionsType',
                component:dynamic({
                    component:()=>import('../page/questionsType'),
                    models:[()=>import('../model/questionsType')],
                    app
                }),
                text:'试题分类',
                groupName:groups.QUESTIONSMANGER.groupName,
                groupIcon:groups.QUESTIONSMANGER.groupIcon,
                id:'main-questionsType'
            },
            {
                path:basePath+'/main/watchQuestions',
                component:dynamic({
                    component:()=>import('../page/watchQuestions'),
                    models:[()=>import('../model/watchQuestions')],
                    app
                }),
                text:'查看试题',
                groupName:groups.QUESTIONSMANGER.groupName,
                groupIcon:groups.QUESTIONSMANGER.groupIcon,
                id:'main-watchQuestions'
            },
            {
                path:basePath + '/main/editQuestions',
                component:dynamic({
                    component:()=>import('../page/editQuestions'),
                    models:[()=>import('../model/addQuestions'),()=>import('../model/watchQuestions')],
                    app
                }),
                showAside:'no',
                text:'编辑试题',
                groupName:groups.QUESTIONSMANGER.groupName,
                groupIcon:groups.QUESTIONSMANGER.groupIcon,
                id:'main-editQuestions'
            },
            {
                path:basePath + '/main/questions/detail',
                component:dynamic({
                    component:()=>import('../page/questionsDetail'),
                    models:[()=>import('../model/addQuestions'),()=>import('../model/watchQuestions')],
                    app
                }),
                showAside:'no',
                text:'试题详情',
                groupName:groups.QUESTIONSMANGER.groupName,
                groupIcon:groups.QUESTIONSMANGER.groupIcon,
                id:'main-questionsDetail'
            },
            // 用户管理
            {
                path:basePath+'/main/addUser',
                component:dynamic({
                    component:()=>import('../page/addUser'),
                    models:[()=>import('../model/addUser'),()=>import('../model/showUser')],
                    app
                }),
                text:'添加用户',
                groupName:groups.USERMANGER.groupName,
                groupIcon:groups.USERMANGER.groupIcon,
                id:'main-addUser'
            },
            {
                path:basePath+'/main/role',
                component:dynamic({
                    component:()=>import('../page/role'),
                    models:[()=>import('../model/role')],
                    app
                }),
                text:'角色管理',
                groupName:groups.USERMANGER.groupName,
                groupIcon:groups.USERMANGER.groupIcon,
                id:'main-role'
            },
            {
                path:basePath+'/main/showUser',
                component:dynamic({
                    component:()=>import('../page/showUser'),
                    models:[()=>import('../model/showUser')],
                    app
                }),
                text:'用户展示',
                groupName:groups.USERMANGER.groupName,
                groupIcon:groups.USERMANGER.groupIcon,
                id:'main-showUser'
            },
            // 考试管理
            {
                path:basePath+'/main/addExam',
                component:dynamic({
                    component:()=>import('../page/addExam'),
                    models:[()=>import('../model/addExam')],
                    app
                }),
                text:'添加考试',
                groupName:groups.EXAMMANGER.groupName,
                groupIcon:groups.EXAMMANGER.groupIcon,
                id:'main-addExam'
            },
            // 班级管理
            {
                path:basePath+'/main/grade',
                component:dynamic({
                    component:()=>import('../page/grade'),
                    models:[()=>import('../model/grade')],
                    app
                }),
                text:'班级管理',
                groupName:groups.GRADEMANGER.groupName,
                groupIcon:groups.GRADEMANGER.groupIcon,
                id:'main-grade'
            },
            {
                path:basePath+'/main/room',
                component:dynamic({
                    component:()=>import('../page/room'),
                    models:[()=>import('../model/room')],
                    app
                }),
                text:'教室管理',
                groupName:groups.GRADEMANGER.groupName,
                groupIcon:groups.GRADEMANGER.groupIcon,
                id:'main-room'
            },
            {
                path:basePath+'/main/student',
                component:dynamic({
                    component:()=>import('../page/student'),
                    models:[()=>import('../model/student')],
                    app
                }),
                text:'学生管理',
                groupName:groups.GRADEMANGER.groupName,
                groupIcon:groups.GRADEMANGER.groupIcon,
                id:'main-student'
            },
            {
                path:basePath+'/main/exam/list',
                component:dynamic({
                    component:()=>import('../page/examList'),
                    models:[()=>import('../model/examList')],
                    app
                }),
                text:'试卷列表',
                groupName:groups.EXAMMANGER.groupName,
                groupIcon:groups.EXAMMANGER.groupIcon,
                id:'main-examList'
            },
            {
                path:basePath+'/main/exam/edit',
                component:dynamic({
                    component:()=>import('../page/editExam'),
                    models:[()=>import('../model/addExam')],
                    app
                }),
                text:'创建试卷',
                groupName:groups.EXAMMANGER.groupName,
                groupIcon:groups.EXAMMANGER.groupIcon,
                showAside:'no',
                id:'main-examEdit'
            },
            {
                path:basePath+'/main/exam/detail',
                component:dynamic({
                    component:()=>import('../page/examDetail'),
                    models:[()=>import('../model/addExam')],
                    app
                }),
                text:'试卷详情',
                groupName:groups.EXAMMANGER.groupName,
                groupIcon:groups.EXAMMANGER.groupIcon,
                showAside:'no',
                id:'main-examDetail'
            }
        ]
    }
]
if(process.env.NODE_ENV === 'development'){
    routerConfig[1].children.push(menu);
}

export default routerConfig;