import dynamic from '../utils/dynamic'
import app from '../app'
import UtilGlobal from '../utils/global'
const {basePath} = UtilGlobal;
const groups = {
    MENU:{
        groupName:'菜单管理',groupIcon:'menu'
    }
}
export default {
    path:basePath+'/main/menu',
    component:dynamic({
        component:()=>import('../page/addMenu'),
        models:[()=>import('../model/addMenu')],
        app
    }),
    text:'添加菜单',
    groupName:groups.MENU.groupName,
    groupIcon:groups.MENU.groupIcon,
    id:'main-menu'
}