import React, { Component } from 'react'
import {BrowserRouter as Router} from 'dva/router'
import RouterView from '../utils/router-view'
import RouterConfig from '../config/router.config'
import Loading from '../components/Loading'
import {connect} from 'dva'
import utilGlobal from '../utils/global'
import { LocaleProvider } from 'antd';
import zh_CN from 'antd/lib/locale-provider/zh_CN'
let mapState = store => {
    return {...store.global}
}

@connect(mapState)
class Root extends Component{
    constructor(props){
        super(props);
        utilGlobal.changeState = (payload)=>{
            if(!this.props.isLoad && payload.isOpen){
                payload = {...payload,isLoad:true}
            }
            this.props.dispatch({type:'global/changeState',...payload});
        }
    }
    render(){
        let {isLoad,isOpen} = this.props;
        return <React.Fragment>
            <LocaleProvider locale={zh_CN}>
            <div>
            {
                isLoad ? <Loading isOpen={isOpen} /> : null
            }
            <Router>
                <RouterView childRoutes={RouterConfig} />
            </Router>
            </div>
            </LocaleProvider>
        </React.Fragment>   
    }
    
}
function RootRouter(){
    return <Root />
}
export default RootRouter;